.directions {
    background: #f3f6fb;
    padding: 7.8125vw 0;
}

.directions__subtitle {
    font-weight: 400;
    color: #666c77;
    max-width: 30.1042vw;
    text-align: center;
    margin: 1.5625vw auto 0 auto;
}

.directions__wrapper {
    margin-top: 3.3854vw;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5625vw;
}
.directions-box {
    background: #ffffff;
    border-radius: 0.625vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 18.2292vw;
    padding: 2.6042vw;
    padding-top: 3.3333vw;
    text-align: center;
    img {
        width: 3.3854vw;
        height: 3.3854vw;
    }
    h3 {
        font-weight: 700;
        color: #4f5561;
        margin-top: 1.4063vw;
    }
    div {
        font-weight: 400;
        color: #666c77;
        margin-top: 1.0417vw;
    }
}

.directions__descr {
    background: linear-gradient(202.45deg, #8432b1 0%, #6a3eb3 100%);
    border-radius: 0.625vw;
    width: 43.3333vw;
    padding: 2.0833vw;
    text-align: center;
    margin: 3.3854vw auto 0 auto;
    font-weight: 400;
    color: #ffffff;
    p {
        margin-bottom: 1.5vw;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.directions-contact {
    padding: 6.7708vw 0;
    .directions-box {
        min-height: 15.1042vw;
        a {
            font-weight: 700;
            color: #4f5561;
        }
    }
    .directions-box img {
        margin-bottom: 1.4063vw;
    }
}

@media (max-width: $table) {
    .directions__wrapper {
        grid-template-columns: 1fr;
        gap: 3.5625vw;
        margin-top: 7.3854vw;
    }
    .directions {
        padding: 10vw 0;
    }
    .directions-box img {
        width: 11.3854vw;
        height: 11.3854vw;
    }
    .directions-box h3 {
        margin-top: 2.4063vw;
        font-size: 5.25vw;
        line-height: 6.5vw;
    }
    .directions-box div {
        margin-top: 2.0417vw;
    }
    .directions-box {
        padding: 4.6042vw;
    }
    .directions__subtitle {
        max-width: 100%;
        margin: 4.5625vw auto 0 auto;
    }
    .directions__descr {
        width: 100%;
        padding: 4.0833vw;
        margin: 8.3854vw auto 0 auto;
    }
    .directions-contact .directions-box {
        min-height: 36.1042vw;
        justify-content: center;
    }
}
